import mqtt, {
  MqttClient,
  IClientSubscribeOptions,
  IClientPublishOptions
} from "mqtt";

import { provide, inject, reactive } from "vue";
import { SubTopic, PubTopic } from "@/plugins/mqtt/topics";
import { getMqttDataCopy, MqttData } from "@/plugins/mqtt/data";
import messageHandler from "@/plugins/mqtt/messageHandler";

interface Device {
  data: MqttData;
  connect: (address: string) => void;
  subscribe: (
    topic: SubTopic | SubTopic[],
    opts: IClientSubscribeOptions
  ) => void;
  unsubscribe: (topic: SubTopic | SubTopic[]) => void;
  publish: (
    topic: PubTopic,
    message: string | ArrayBuffer | Uint8Array,
    opts: IClientPublishOptions
  ) => void;
}

let client = null as null | MqttClient;

const device: Device = {
  data: reactive(getMqttDataCopy()),
  connect(address: string) {
    if (client) client.end();
    const clientId =
      "UApp_" +
      Math.random()
        .toString(16)
        .substr(2, 8);
    client = mqtt.connect(address, {
      clientId: clientId,
      keepalive: 5,
      will: {
        topic: "appStatus",
        payload: "disconnect_" + clientId,
        retain: true,
        qos: 1
      }
    });
    client.on("connect", function() {
      device.data.mqttConnected = true;
      device.subscribe(
        [
          "slam/run",
          "usys/run",
          "bms/state",
          "firmware/version",
          "controller/run"
        ],
        { qos: 1 }
      );
    });
    client.on("message", function(topic, message) {
      messageHandler(topic, message, device.data);
    });
    client.on("close", function() {
      Object.assign(device.data, getMqttDataCopy());
    });
  },
  subscribe(topic: SubTopic | SubTopic[], opts: IClientSubscribeOptions) {
    client?.subscribe(topic, opts);
  },
  unsubscribe(topic: SubTopic | SubTopic[]) {
    client?.unsubscribe(topic);
  },
  publish(
    topic: PubTopic,
    message: string | ArrayBuffer | Uint8Array,
    opts: IClientPublishOptions
  ) {
    if (typeof message === "string" || message instanceof Uint8Array) {
      client?.publish(topic, message as string | Buffer, opts);
    } else if (message instanceof ArrayBuffer) {
      client?.publish(topic, new Uint8Array(message) as Buffer, opts);
    }
  }
};

const DeviceSymbol = Symbol();

const MqttPlugin = {
  provide: () => {
    const wsProtocol =
      window.location.protocol === "https:" ? "wss://" : "ws://";
    if (process.env.NODE_ENV == "development") {
      device.connect(wsProtocol + process.env.VUE_APP_DEVICE_ADDRESS + "/mqtt");
    } else {
      device.connect(wsProtocol + window.location.host + "/mqtt");
    }

    provide(DeviceSymbol, device);
  },
  use: (): Device => {
    const device = inject(DeviceSymbol) as Device;
    if (!device) throw new Error("No mqtt provided!!!");
    return device;
  }
};

export default MqttPlugin;
