import { MqttData } from "@/plugins/mqtt/data";
import { SlamSubTopic } from "@/plugins/mqtt/topics";

type Receivers = {
  [key in SlamSubTopic]: (
    data: MqttData,
    message: Buffer,
    dataView: DataView
  ) => void;
};

const receivers: Receivers = {
  "slam/position": (data, message, dataView) => {
    const getFloats = (index: number) => dataView.getFloat64(index * 8, true);
    data.slam.pos = {
      position: { x: getFloats(0), y: getFloats(1), z: getFloats(2) },
      orientation: {
        x: getFloats(3),
        y: getFloats(4),
        z: getFloats(5),
        w: getFloats(6)
      }
    };
  },
  "slam/targets": (data, message, dataView) => {
    const getFloats = (index: number) => dataView.getFloat64(index * 8, true);
    data.slam.targets = [];
    for (let i = 0; i < message.byteLength / 8; i += 7) {
      data.slam.targets.push({
        position: { x: getFloats(i), y: getFloats(i + 1), z: getFloats(i + 2) },
        orientation: {
          x: getFloats(i + 3),
          y: getFloats(i + 4),
          z: getFloats(i + 5),
          w: getFloats(i + 6)
        }
      });
    }
  },
  "slam/run": (data, message) => {
    data.slam.run = message.byteLength > 0;
    if (message.byteLength === 0) data.slam.targets = [];
  },
  "slam/target_index": (data, message, dataView) => {
    data.slam.targetIndex = message.byteLength > 0 ? dataView.getUint8(0) : 0;
  },
  "slam/looping": (data, message) => {
    data.slam.looping = message.byteLength > 0;
  }
};

export default receivers;
