import { Target } from "slam2d";
export type AiMode = "MNFH" | "cam1" | "cam2" | "cam3" | "cam4" | "cam5";

export interface MqttData {
  mqttConnected: boolean;
  managerOn: boolean;
  controllerOn: boolean;
  usys: {
    isUpdating: boolean;
    deletedFile: string;
    updateProgress: number;
    updateMsg: string;
  };
  slam: {
    pos: Target;
    targets: Target[];
    run: boolean;
    looping: boolean;
    targetIndex: number;
  };
  bms: {
    version: string;
    status: number;
    soc: number;
    current: number;
    cycle: number;
    temps: number[];
    voltage: number;
    cellVoltages: number[];
  };
  robot: {
    sn: {
      product: string;
      id: string;
    };
    version: {
      hardware: string;
      software: string;
    };
    temps: number[];
    mode: number;
    gaitType: number;
    obstacles: number[];
    state: string;
    distanceWarning: {
      front: number;
      back: number;
      left: number;
      right: number;
    };
  };
  vision: {
    aiMode: AiMode;
  };
}

const data: MqttData = {
  mqttConnected: false,
  managerOn: false,
  controllerOn: false,
  usys: {
    isUpdating: false,
    deletedFile: "",
    updateProgress: 0,
    updateMsg: ""
  },
  slam: {
    pos: {
      position: { x: 0, y: 0, z: 0 },
      orientation: { x: 0, y: 0, z: 0, w: 0 }
    },
    targets: [] as Target[],
    run: false,
    looping: false,
    targetIndex: 0
  },
  vision: {
    aiMode: "MNFH"
  },
  bms: {
    version: "unknown",
    status: 0,
    soc: 0,
    current: 0,
    cycle: 0,
    temps: new Array(4).fill(0),
    voltage: 0,
    cellVoltages: new Array(10).fill(0)
  },
  robot: {
    version: {
      hardware: "--",
      software: "--"
    },
    sn: {
      product: "--",
      id: "--"
    },
    temps: new Array(20).fill(0),
    mode: 0,
    gaitType: 0,
    obstacles: [255, 255, 255, 255],
    state: "invalid",
    distanceWarning: {
      front: 0,
      back: 0,
      left: 0,
      right: 0
    }
  }
};

const dataCopy = JSON.stringify(data);

export const getMqttDataCopy = () => {
  return JSON.parse(dataCopy) as MqttData;
};
