import { MqttData } from "@/plugins/mqtt/data";
import { SubTopic } from "@/plugins/mqtt/topics";
import slamReceivers from "@/plugins/mqtt/receivers/slamReceivers";
import usysReceivers from "@/plugins/mqtt/receivers/usysReceivers";
import visionReceivers from "@/plugins/mqtt/receivers/visionReceivers";
import bmsReceivers from "@/plugins/mqtt/receivers/bmsReceivers";
import robotReceivers from "@/plugins/mqtt/receivers/robotReceivers";
import controllerReceivers from "@/plugins/mqtt/receivers/controllerReceivers";
// import AnsiUp from "ansi_up";
// const ansiUp = new AnsiUp();

type Receivers = {
  [key in SubTopic]: (
    data: MqttData,
    message: Buffer,
    dataView: DataView
  ) => void;
};

const messageReceivers: Receivers = {
  ...slamReceivers,
  ...usysReceivers,
  ...visionReceivers,
  ...bmsReceivers,
  ...robotReceivers,
  ...controllerReceivers
};

export default function messageHandler(
  topic: string,
  message: Buffer,
  data: MqttData
) {
  const msgTopic = topic as SubTopic;
  const dataView = new DataView(
    message.buffer,
    message.byteOffset,
    message.byteLength
  );
  const receiver = messageReceivers[msgTopic];
  if (receiver) receiver(data, message, dataView);
  else console.log(message);
}

