import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import Home from "../views/Home.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/bms",
    name: "Bms",
    component: () => import("../views/Bms.vue")
  },
  {
    path: "/addons",
    name: "Addons",
    component: () => import("../views/Addons.vue")
  },
  {
    path: "/status",
    name: "Status",
    component: () => import("../views/Status.vue")
  },
  {
    path: "/vision",
    name: "Vision",
    component: () => import("../views/Vision.vue")
  },

  {
    path: "/visiontest",
    name: "VisionTest",
    component: () => import("../views/VisionTest.vue")
  },
  {
    path: "/slam",
    name: "Slam",
    component: () => import("../views/Slam.vue")
  },
  {
    path: "/simulator",
    name: "SimulatorPage",
    component: () => import("../views/SimulatorPage.vue")
  },
  {
    path: "/update",
    name: "UpdatePage",
    component: () => import("../views/Update.vue")
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;
