import { MqttData, getMqttDataCopy } from "@/plugins/mqtt/data";
import { ControllerSubTopic } from "@/plugins/mqtt/topics";

type Receivers = {
  [key in ControllerSubTopic]: (
    data: MqttData,
    message: Buffer,
    dataView: DataView
  ) => void;
};

const receivers: Receivers = {
  "controller/run": (data, message) => {
    data.controllerOn = message.byteLength > 0;
    if (!data.controllerOn) {
      const dataCopy = getMqttDataCopy();
      data.bms = dataCopy.bms;
      data.robot = dataCopy.robot;
    }
  },
  "controller/current_action": (data, message) => {
    console.log(message);
  },
  "controller/progress": (data, message) => {
    console.log(message);
  }
};

export default receivers;
