import { MqttData, AiMode } from "@/plugins/mqtt/data";
import { VisionSubTopic } from "@/plugins/mqtt/topics";

type Receivers = {
  [key in VisionSubTopic]: (
    data: MqttData,
    message: Buffer,
    dataView: DataView
  ) => void;
};

const receivers: Receivers = {
  "vision/ai_mode": (data, message) => {
    data.vision.aiMode = message.toString() as AiMode;
  }
};

export default receivers;
