
import { defineComponent } from "vue";
import I18nPlugin from "@/plugins/i18nPlugin";
import MqttPlugin from "@/plugins/mqttPlugin";
export default defineComponent({
  setup() {
    const i18n = I18nPlugin.use();
    const mqtt = MqttPlugin.use();
    console.log(process.env);

    const executeSH = (sh: string) => {
      mqtt.publish("usys/sh", sh, { qos: 1 });
    };
    return { i18n, mqtt, executeSH };
  }
});
