import { MqttData } from "@/plugins/mqtt/data";
import { UsysSubTopic } from "@/plugins/mqtt/topics";
import AnsiUp from "ansi_up";
const ansiUp = new AnsiUp();

type Receivers = {
  [key in UsysSubTopic]: (
    data: MqttData,
    message: Buffer,
    dataView: DataView
  ) => void;
};

const receivers: Receivers = {
  "usys/run": (data, message) => {
    data.managerOn = message.byteLength > 0;
  }
};

export default receivers;
